.container {
  padding: 15px;
}

.title {
  color: crimson;
  margin-bottom: 10px;
}

.poster {
  width: 300px;
  min-height: 400px;
  margin-bottom: 5px;

  border-radius: 5px;
  border: 1px solid rgba(0, 0, 255, 0.5);
}

.movieItem {
  margin-bottom: 5px;
  font-size: 16px;
  font-weight: 500;
}

.descript {
  padding-left: 5px;
  padding-right: 5px;
  color: blue;
}

.vote {
  /* composes: descript; */
  display: inline-block;
  min-width: 40px;
  margin-left: 5px;
  margin-right: 5px;

  text-align: center;
  background-color: orange;
  border-radius: 4px;
}

.subLinks {
  display: inline-block;
  margin-right: 20px;
  padding: 5px 10px;
  background-color: blue;
  color: white;
  border-radius: 5px;
}

.activeSubLinks {
  background-color: rgba(0, 0, 255, 0.5);
}

.subLinks:hover,
.backButton:hover {
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}